<template>
  <!--만족도 조사-->
  <div class="chatStar">
    <div class="chatStarImgs">
      <div class="chkStar" @click="onClickStar">
        <span class="chk chkStar01">
          <input type="radio" id="chkpointL01" name="chkpointL" :disabled="disable" :checked="'chkpointL01' === checked">
          <label :class="disable ? 'disable' : 'star-label'" for="chkpointL01">별점 5점 중 1점</label>
        </span>
        <span class="chk chkStar02">
          <input type="radio" id="chkpointL02" name="chkpointL" :disabled="disable" :checked="'chkpointL02' === checked">
          <label :class="disable ? 'disable' : 'star-label'" for="chkpointL02">별점 5점 중 2점</label>
        </span>
        <span class="chk chkStar03">
          <input type="radio" id="chkpointL03" name="chkpointL" :disabled="disable" :checked="'chkpointL03' === checked">
          <label :class="disable ? 'disable' : 'star-label'" for="chkpointL03">별점 5점 중 3점</label>
        </span>
        <span class="chk chkStar04">
          <input type="radio" id="chkpointL04" name="chkpointL" :disabled="disable" :checked="'chkpointL04' === checked">
          <label :class="disable ? 'disable' : 'star-label'" for="chkpointL04">별점 5점 중 4점</label>
        </span>
        <span class="chk chkStar05">
          <input type="radio" id="chkpointL05" name="chkpointL" :disabled="disable" :checked="'chkpointL05' === checked">
          <label :class="disable ? 'disable' : 'star-label'" for="chkpointL05">별점 5점 중 5점</label>
        </span>
      </div>
      <div class="chkStarTxts">{{ $t(`satisfactionSurvey.${chatTxt}`) }}</div>
    </div>
    <div class="chatStarTxts">
      <textarea :disabled="disable" :placeholder="$t('satisfactionSurvey.placeholder')" :value="message" @input="setMessage"></textarea>
      <p>{{ message.length }}/{{MAX_LENGTH}}</p>
    </div>
    <ul class="chatStarBtns">
      <li><a role="button" :class="disable ? 'disable' : ''" @click="onCancel">{{ $t('satisfactionSurvey.cancel') }}</a></li>
      <li><a role="button" :class="disable ? 'disable' : ''" @click="onClickOk">{{ $t('satisfactionSurvey.ok')}}</a></li>
    </ul>
  </div>
  <!--만족도 조사-->
</template>

<script>
export default {
  name: 'SatisfactionSurvey',
  data() {
    return {
      chatTxt: 'very_good',
      score: 100,
      checked: 'chkpointL05',
      message: '',
      MAX_LENGTH: 100,
      disable: false,
    };
  },
  watch: {
    message(current, old) {
      if (!current) {
        return;
      }

      if (current.length > this.MAX_LENGTH) {
        this.message = old;
      }
    },
  },
  methods: {
    setMessage(e) {
      const MAX_LENGTH = 100;
      if (e.target.value > MAX_LENGTH) {
        return;
      }

      this.message = e.target.value;
    },
    reset() {
      this.score = 100;
      this.message = '';
      this.checked = 'chkpointL05';
      this.chatTxt = 'very_good';
    },
    onCancel() {
      if (this.disable) {
        return;
      }

      if (typeof this.$store.state.enomix.surveyCallback === 'function') {
        this.$store.state.enomix.surveyCallback();
      }

      this.disable = true;
      this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_194);
      this.$store.commit('RESET_CONSULTING_INFO');
    },
    async onClickOk() {
      if (this.disable) {
        return;
      }

      if (!this.score) {
        return;
      }

      this.disable = true;
      this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_195);
      const result = await this.$store.dispatch('SEND_SURVEY_RESULT', { score: this.score, message: this.message });
      if (!result) {
        this.disable = false;
      }
    },
    onClickStar(e) {
      if (this.disable) {
        return;
      }

      const id = e.target.id || e.target.htmlFor;
      this.checked = id;
      switch (`${id || ''}`.split('chkpointL0')[1]) {
        case '1':
          this.chatTxt = 'very_bad';
          this.score = 20;
          break;
        case '2':
          this.chatTxt = 'bad';
          this.score = 40;
          break;
        case '3':
          this.chatTxt = 'normal';
          this.score = 60;
          break;
        case '4':
          this.chatTxt = 'good';
          this.score = 80;
          break;
        case '5':
          this.chatTxt = 'very_good';
          this.score = 100;
          break;

        // no default
      }
    },
  },
};
</script>

<style>
.chatStarBtns li a {
  cursor: pointer !important;
}

.chatStarBtns li a.disable {
  cursor: not-allowed !important;
}

.chatStarImgs .star-label {
  cursor: pointer !important;
}

.chatStarImgs .disable {
  cursor: not-allowed !important;
}
</style>